<template>
  <div class="container">
    <div id="loadingDiv">
      <div class="loader"></div>
    </div>

    <div class="Project_product">
      <div class="bar__container">
        <ul class="bar row" id="bar">
          <li class="active">STEP 01 購物車</li>
          <li class="active">STEP 02 填寫資料</li>
          <li class="active">STEP 03 訂購完成</li>
        </ul>
      </div>
      <div class="row pt-5 pb-5">
        <div class="col-lg-8 form-test">
          <h5>訂單資訊</h5>
          <hr />
          <div style="margin-left: 15px; line-height: 30px; font-size: 17px">
            <div class="row">
              <div class="col-3">
                <div><b>訂單編號</b></div>
                <div><b>訂購日期</b></div>
              </div>
              <div class="col-9">
                <div>{{ test.created.timestamp }}</div>
                <div>
                  {{ test.created.datetime }}
                  <span class="badge badge-pill badge-primary ml-1">{{
                    test.created.diff
                  }}</span>
                </div>
              </div>
            </div>
          </div>
          <div style="height: 30px"></div>
          <h5>顧客資訊</h5>
          <hr />
          <div style="margin-left: 15px; line-height: 30px; font-size: 17px">
            <div class="row">
              <div class="col-3">
                <div><b>收件人</b></div>
                <div><b>收件人地址</b></div>
                <div><b>收件人手機</b></div>
                <div><b>電子信箱</b></div>
              </div>
              <div class="col-9">
                <div>{{ test.user.name }}</div>
                <div>{{ test.user.address }}</div>
                <div>{{ test.user.tel }}</div>
                <div>{{ test.user.email }}</div>
              </div>
            </div>
          </div>

          <div style="height: 30px"></div>
          <h5>商品資訊</h5>
          <table class="table" style="margin-top: 1rem">
            <thead style="background-color: #eee">
              <tr>
                <th scope="col">品名</th>
                <th scope="col" width="150">數量</th>
                <th scope="col" width="120">小計</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item, key) in test.products"
                :load="log(item)"
                :key="key"
              >
                <td class="align-middle" scope="row">
                  <img
                    :src="item.product.imageUrl"
                    class="img-fluid mr-3 d-none d-md-inline-block"
                    width="80px"
                  />
                  {{ item.product.title }}
                </td>
                <td class="align-middle">
                  {{ item.quantity }}
                </td>
                <td class="align-middle">
                  $ {{ item.product.price * item.quantity }}
                </td>
              </tr>
              <tr>
                <td></td>
                <td></td>
                <td class="h4 font-weight-bold"><span class="mr-2" style="font-size: 15px"> 總金額 </span><span class="mr-2" style="font-size: 15px"> NT$</span><span style="font-size: 1.5rem;">{{ test.amount }}</span><span class="ml-2" style="font-size: 15px">元</span></td>
              </tr>
            </tbody>
          </table>
          <a
            href="#"
            @click.prevent="
              category = '所有商品';
              toProducts();
            "
            ><button class="btn btn-success btn-block" style="cursor: pointer;">繼續購物</button></a
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "MyCheck",
  data() {
    return {
      list: [],
      user: [],
      test: {},
    };
  },
  created() {
    this.getList();
  },
  methods: {
    log(item) {
      console.log("V-FOR");
      console.log(item);
    },

    toProducts() {
      this.$emit("fliterProduct", this.category);
      this.$router.push(`/products/${this.category}`);
    },

    getList() {
      const api =
        "https://course-ec-api.hexschool.io/api/d25970ec-b87b-44f6-9785-9bdbbcd8dc56/ec/orders";
      this.$http.get(api).then((res) => {
        this.list = res.data.data;
        console.log("LIST");
        console.log(this.list);
        this.getUser();
        this.value = false;
      });
    },
    getUser() {
      const api = `https://course-ec-api.hexschool.io/api/d25970ec-b87b-44f6-9785-9bdbbcd8dc56/ec/orders/${this.list[0].id}`;
      this.$http.get(api).then((res) => {
        this.test = res.data.data;
        console.log("test");
        console.log(this.test);
      });
    },
  },
};
</script>

